import React from "react"
import Link from 'gatsby-link'
import TagList from '../components/common/TagList';
import NavbarFixed from '../components/NavbarFixed';
import '../styles/blog.css';
import Footer from '../components/Footer';

const styles = {
  description: {
    color: '#576366',
  },
  timeToRead: {
    float: 'right'
  },
  tagsContainer: {
    flexDirection: 'row',
    display: 'flex',
    marginTop: 10,
    fontSize: 14,
  },
  tag: {
    marginRight: 10,
    backgroundColor: 'grey',
    padding: 5,
    paddingLeft: 8,
    paddingRight: 8,
    borderRadius: 6,
    fontWeight: 'bold',
  },
  pageLink: {
  },
  pagination: {
    marginTop: 30,
    marginBottom: 30,
    display: 'flex',
    justifyContent: 'space-between',
  },
};

export default function BlogIndex({ data, pathContext }) {
  const { edges: posts } = data.allMarkdownRemark;
  const { numPages = 0, currentPage = 1 } = pathContext;
  const isFirst = currentPage === 1;
  const isLast = currentPage === numPages;
  const prevPage = currentPage - 1 === 1 ? "" : (currentPage - 1).toString();
  const nextPage = (currentPage + 1).toString();

  return (
    <div className="blog-posts content">
      <NavbarFixed
        title="BLOG"
        canGoBack={true}
        backToUrl='/'
      />

      {posts
        .filter(post => post.node.frontmatter.title.length > 0)
        .map(({ node: post }) => {
          return (
            <div className="row" key={post.id}>
              <div className="blog-post-preview col-md-8 offset-md-2">
                  <Link
                    style={{ color: 'black'}}
                    className="blog-post-preview-link"
                    to={post.frontmatter.path}>
                    <h4>
                      {post.frontmatter.title}
                    </h4>
                    <h6 style={styles.description}>{post.frontmatter.date}</h6>
                    <div>
                      <span style={styles.description}>{post.frontmatter.excerpt}</span>
                      <span style={styles.timeToRead}>{post.timeToRead} min read</span>
                    </div>
                    <TagList data={post.frontmatter.tags} />
                  </Link>
              </div>
            </div>
          );
        })
      }

      <div className="col-md-8 offset-md-2" style={styles.pagination}>
        {!isFirst && (
           <Link
              to={`/blog/${prevPage}`}
              className="btn btn-outline-primary"
              style={styles.pageLink}
            >
             ← Previous Page
           </Link>
         )}
         {!isLast && (
           <Link
            to={`/blog/${nextPage}`}
            className="btn btn-outline-primary"
            style={styles.pageLink}
           >
             Next Page ({`${currentPage + 1} / ${numPages}`}) →
           </Link>
         )}
      </div>
      <Footer />
    </div>
  );
}

export const blogListQuery = graphql`
  query blogListQuery($skip: Int!, $limit: Int!) {
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          id
          timeToRead
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            excerpt
            tags
          }
        }
      }
    }
  }
`



